import comHeader from '@/components/header/header'
import { getApplyList } from '@/api/salary'
import cTable from './components/table'
export default {
  name: 'salaryApply',
  components: {
    comHeader,
    cTable
  },
  data () {
    return {
      list: [],
      limit: 20,
      page: 1
    }
  },
  beforeMount () {
    this._onDataList()
  },
  methods: {
    async _onDataList () {
      let params = {
        limit: this.limit,
        page: this.page
      }
      getApplyList(params).then(({ data: { code, data = {}, msg } }) => {
        if (code === 1 && data.list && data.list.length) {
          this.list = data.list
        }
      }).catch((error) => {
        console.log(error)
      })
    }
  }
}



